*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    width: 100vw;
    background-color: hsl(180, 52%, 96%);
    font-family: 'League Spartan', sans-serif;
    font-weight: 500;
}

header{
    width: 100%;
    height: 200px;
    background-image: url('./images/bg-header-desktop.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-color:  hsl(180, 29%, 50%);
}

ul{
    list-style-type: none;
}

.container{
    display: flex;
    flex-direction: column;
    padding: 4rem;
    gap: 1rem;
}

.job{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 2rem;
    background-color: white;
    border-radius: 5px;
}

.job--left-content{
    display: flex;
    flex-direction: row;
    gap: 2rem;
}

.featured{
    border-left: solid 4px hsl(180, 29%, 50%);
}

.job--profile{
    width: 125px;
}

.job--main-data{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: .5rem;
    
}

.job--main-data-agency{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    align-items: center;
}

.job--agency-name{
    color: hsl(180, 29%, 50%);
}

.job--main-data-desc{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.job--main-data-desc li{
    color: hsl(180, 8%, 52%);
}

.job--agency-new{
    color: white;
    background-color: hsl(180, 29%, 50%);
    padding: .25rem .5rem;
    border-radius: 100px;
}

.job--agency-featured{
    color: white;
    background-color: black;
    padding: .25rem .5rem;
    border-radius: 100px;
}

.job--main-data-desc{
    display: flex;
    flex-direction: row;
}

.job--main-attribute{
    display: flex;
    flex-direction: row;
    gap: 2rem;
}

.job--main-attribute li {
    padding: .5rem 1rem;
    border-radius: 5px;
    background-color: hsl(180, 52%, 96%);
    color: hsl(180, 29%, 50%);
    font-weight: 700;
}

hr{
    display: none;
}

.job--agency-new-null,
.job--agency-featured-null{
    display: none;
}

@media screen and (max-width:1280px){

    .container{
        padding: 1rem;
    }

    .job{
        position: relative;
        flex-direction: column;
        align-items:unset;
        gap: 1rem;
    }

    .job--profile{
        position: absolute;
        bottom: 90%;
        width: 50px;
    }

    .job--main-data-name{
        font-size: 1rem;
    }

    hr{
        display: block;
        color: hsl(180, 8%, 52%);
        border: none;
        height: 1px;
        background-color: hsl(180, 8%, 52%);
    }

    .job--main-attribute{
        max-width: 350px;
        flex-wrap: wrap;
    }
}